.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}

.title{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: rgb(42, 52, 80);
    text-decoration: underline;

}
.calories{
    font-weight: bolder;
    border: solid 1px black;
    padding: 2px;

}
.image{
    border-radius: 30px;
    height: 200px;
}
.list{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.ingredientlist{
    font-family: cursive;
}