.App {
  min-height: 100vh;
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}

.hh{
  display: flex;
  justify-content:space-around
}
.heading{
  align-self: center;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color:#3a4e09 ;
 
}
.img{
  height: 80px;
  width: 80px;
}
.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 5px;
}
.search-button{
  background: #95c22b;
  padding: 10px;
  margin-left: 10px;
  border: none;
  border-radius: 5px;
  padding-left: 20px;
  padding-right: 20px;

}
.search-button:hover{
  cursor: pointer;
  text-decoration: underline;
}

.recipes{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;


}
.recipe_recipe__fp-qL{
  height: 800px;
 width: 600px;
}